.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-left: 0;

  &__item {
    width: 250px;
    margin: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  &__link {
    text-decoration: none;
    color: #022147;
    font-weight: 400px;
  }

  &__title {
    display: block;
    text-align: center;
    padding: 20px;
  }
}
